.Header {
    margin-top: 13rem;
    position: relative;
    z-index: 1;
    
    &__wrapper {
        height: 150rem;
        background-color: #fff;
        box-shadow: 0 0 5rem rgba(#000, .1);
    }

    &__hero {
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 3.5rem 0;
        background-color: #f7f7f7;
    }
}