.Dropdown {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-out;
    background: #fff;
    font-size: 1.3rem;
    border-radius: 0 0 .5rem .5rem;
    color: #616161;
    box-shadow: 0 0 3rem rgba(#000, .07);
    border: 1px solid rgba(#000, .12);
    width: 20rem;
    font-weight: 500;
    overflow: hidden;

    &::after {
        content: "";
        position: absolute;
        top: -100%;
        left: 50%;
        transform: translateX(-50%);
        border-width: .7rem;
        border-style: solid;
        border-color: #fff transparent transparent transparent;
    }
    
    &--left-fix {
        left: 0;
        transform: translateX(0);
    }
    
    &--right--fix {
        right: 0;
        transform: translateX(0);
        left: initial;
    }
    
    &--small {
        box-shadow: 0 0 3rem rgba(#000, .07);
        height: 30rem;
        border-bottom: 1px solid rgba(#000, .07);
    }

    &--show {
        opacity: 1;
        visibility: visible;
    }

    &--full { width: 100%; }

    &--big {
        border-top: none;
        font-size: 1.4rem;
    }

    &__wrapper {
        position: absolute;
        height: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: -17px;
        overflow: auto;
    }

    &:hover {
        opacity: 1;
        visibility: visible;
    }

    &__full {
        height: 50rem;
        width: 100%;
    }
    
    &__title {
        color: #a5a5a5;
        padding: 1.2rem 2.5rem;
    }

    &__link {
        display: flex;
        padding: 1.2rem 2.5rem;
        
        &:hover {
            background: #0652DD;
            color: #fff;
        }
    }
    
    &__btn {
        padding: 1.2rem 2.5rem;
        border-top: 1px solid rgba(#000, .12);
        width: 100%;
        font-weight: 500;

        &:hover {
            background: #0652DD;
            color: #fff;

        }
        
        &--grey:hover {
            background: #e55039;
        }
    }
}