.Searchbar {
    display: flex;
    align-items: stretch;
    position: relative;

    &__input {
        font-size: 1.5rem;
        font-weight: 500;
        padding: 1.3rem 3.5rem;
        width: 35rem;
        border: 1px solid rgba(#000, .1);
        
        &::-webkit-input-placeholder {
            font-size: 1.5rem;
        }
    }
    
    &__btn {
        padding: 0 2.5rem;
        font-size: 1.5rem;
        font-weight: 500;
        display: flex;
        justify-content: center;
        transition: all .15s ease-in-out;
        background: #fff;
        border: 1px solid rgba(#000, .1);
        border-left: 1px solid rgba(#000, .1);
        position: relative;
        
        &:hover {
            .Searchbar__title {
                color: #0652DD;
            }

            .Searchbar__icon {
                fill: #0652DD;
            }
        }
        
        &--map {
            display: flex;
            align-items: center;
            color: #616161;
            border-radius: 0;
            border: none;
            border-top: 1px solid rgba(#000, .1);
            border-bottom: 1px solid rgba(#000, .1);
            
            &:hover .Dropdown {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    
    &__title {
        width: 14rem;
        transition: color .15s ease-in-out;
    }

    &__icon {
        width: 3rem;
        height: 3rem;
        transition: fill .3s ease;
        fill: #616161;
        
        &--map {
            width: 2.3rem;
            height: 2.3rem;
            margin-right: 1rem;
        }
    }
}
